@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";

@import "variables";

// Main Banner

.cathay-carosel{
  &.#{$prefix}-layout-01-s{
    .slick-slide{
      position: relative;
    }
    .#{$prefix}txt-wrap{
      position: absolute;
      top:0;
      bottom:0;
      left:0;
      right: 0;
      display: block;
      width: 100%;
      padding:30px 25px;

      @media (min-width: map-get($grid-breakpoints, 'md')){
        padding: 20px 20px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        padding: 50px 20px;
        max-width: 960px;
        margin: auto;
        right: 0;
      }

      @media (min-width: map-get($grid-breakpoints, 'xl')){
        max-width: 1140px;
      }
    }

    
    
    .#{$prefix}title{
      font-size:28px;
      line-height: 34px;
      font-weight: bold;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        font-size:50px;
        line-height: 58px;
        max-width:500px;
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }
    }
    .#{$prefix}subtitle{
      font-size:20px;
      line-height: 28px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        font-size:24px;
        line-height: 36px;
        max-width:510px;
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }
    }
    .#{$prefix}link{
      position: absolute;
      top:0;
      left:0;
      width: 100%;
      height: 100%;
      background-color: #ff0000;
      opacity:0;
    }

    .#{$prefix}content{
      font-size:16px;
      line-height: 24px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        font-size:18px;
        line-height: 28px;
        width: 80%;
        display: block;
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        width: 50%;
      }
    }

    .#{$prefix}more{
      display: block;
      margin-top: 25px;

      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin-top: 15px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        margin-top:40px;

      }
    }

    
    .#{$prefix}title + .#{$prefix}subtitle{
      margin-top: 10px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin-top: 10px;
      }
    }
    .#{$prefix}subtitle + .#{$prefix}content{
      margin-top: 10px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin-top: 10px;
      }
    }
    
    .#{$prefix}subtitle + .#{$prefix}title{
      margin-top: 10px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin-top: 10px;
      }
    }

    .#{$prefix}title + .#{$prefix}content{
      margin-top: 10px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin-top: 10px;
      }
    }

    .#{$prefix}content + .#{$prefix}title{
      margin-top: 10px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin-top: 10px;
      }
    }

    .#{$prefix}content + .#{$prefix}subtitle{
      margin-top: 10px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin-top: 10px;
      }
    }



    .#{$prefix}img-wrap{
      width: 100%;
      height: 310px;
      vertical-align:middle;
      background-size: cover;
      background-position: center center;
      
      @media (min-width: map-get($grid-breakpoints, 'md')){
        height: 240px;
      }
      
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        height: 310px;
      }
      img{
        visibility:hidden;
      }
    }

    
    .slick-dots{
      bottom:15px;

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        bottom:20px;
      }
      li{
        width: 6px;
        margin: 0 8px;
        transition: width .15s ease-in-out;
        button{
          padding:3px;
          &:hover,&:focus{
            border:none;
          }
        }

        &.slick-active{
          width: 64px;
        }


        @media (min-width: map-get($grid-breakpoints, 'md')){
          width: 8px;
          margin: 0 10px;
          button{
            padding:4px;
          }
        }
      }


    }
  }
}